@import url(https://fonts.googleapis.com/css2?family=Teko:wght@300&display=swap);
body {
  margin: 0;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root,
#root > div {
    height: 100%;
    font-family: 'Teko', sans-serif;
    font-size: 16px;
}

.gamescreen{
  width: 100vw;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
  background-color: black;
}
.board {
    background-color: black;
    cursor: none;
    overflow: hidden;
    position: relative;
}

.GameStatus{
    width: 100%;
    top: 0;
    left: 0;
    height: 30px;
    position: absolute;
    -webkit-backdrop-filter: blur(18px);
            backdrop-filter: blur(18px);
    color: white;
    display: grid;
    grid-template-columns: 30% 30% 30%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-family: 'Courier New', Courier, monospace;
    grid-column-gap: 1%;
    -webkit-column-gap: 1%;
            column-gap: 1%;
    text-align: center;
    z-index: 2;
    font-size: 0.8rem;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.shotAlert{
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    background: radial-gradient(transparent 70%, red 100%);
    -webkit-animation: pulsatingWound 100ms forwards;
            animation: pulsatingWound 100ms forwards;
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
}
.shakingScreen{
    -webkit-animation: shake 100ms infinite;
            animation: shake 100ms infinite;

}
@-webkit-keyframes shake{
    25%{
        -webkit-transform: translateX(-5px);
                transform: translateX(-5px);
    }
    75%{
        -webkit-transform: translateX(5px);
                transform: translateX(5px);
    }
}
@keyframes shake{
    25%{
        -webkit-transform: translateX(-5px);
                transform: translateX(-5px);
    }
    75%{
        -webkit-transform: translateX(5px);
                transform: translateX(5px);
    }
}

@-webkit-keyframes pulsatingWound {
    0%{
        opacity: 0;
    }
    
    50%{
        opacity: 1;
    }

    100%{
        opacity: 0;
    }
}

@keyframes pulsatingWound {
    0%{
        opacity: 0;
    }
    
    50%{
        opacity: 1;
    }

    100%{
        opacity: 0;
    }
}

.gameboard{
    border: 1px solid rgba(255, 255, 255, 0.24);
    /* animation: shadowColoring 5s linear infinite; */
}
@-webkit-keyframes shadowColoring{
    0%{
        box-shadow: 0 0 10px 2px rgba(255, 214, 139, 0.3);
    }
    100%{
        box-shadow: 0 0 10px 2px rgb(222, 155, 253, 0.3);
    }
    0%{
        box-shadow: 0 0 10px 2px rgb(255, 214, 139,0.3);
    }
}
@keyframes shadowColoring{
    0%{
        box-shadow: 0 0 10px 2px rgba(255, 214, 139, 0.3);
    }
    100%{
        box-shadow: 0 0 10px 2px rgb(222, 155, 253, 0.3);
    }
    0%{
        box-shadow: 0 0 10px 2px rgb(255, 214, 139,0.3);
    }
}
